'use client';

import { useState } from 'react';
import css from './Search.module.css';
import Select from 'react-select';

const options = [
  { value: 'chocolate', label: 'Chocolate' },
  { value: 'strawberry', label: 'Strawberry' },
  { value: 'vanilla', label: 'Vanilla' },
];

const Search = () => {
  const [isLoading, setIsLoading] = useState(false);

  const [options, setOptions] = useState<{ value: string; label: string }[]>([]);

  useEffect(() => {
    const fetchOptions = async () => {
      const brands = await getBrands();
      // Трансформуємо масив рядків у формат для react-select
      const formatted = brands.map(brand => ({ value: brand, label: brand }));
      setOptions(formatted);
    };
    fetchOptions();
  }, []);

  return (
    <div className={css.allFilters}>
      <Select options={options} />

      <Select options={options} isLoading={isLoading} />

      <div className={css.carMileageInput}>
        <input></input>
        <input></input>
      </div>

      <button className={css.searchBtn}>Search</button>
    </div>
  );
};

export default Search;
